.dropdown {
  display: none;
  padding: 10px;
  position: absolute;
  top: 100%;
  color: #ffdbc3;
  left: 50%; /* Center horizontally relative to the parent */
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%); /* Adjust horizontally to center */
  background: #3d246c;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);
